.slideCaptchaContainer{position:relative;margin:0 auto}.slideCaptchaContainer .panel{position:absolute;margin:0 auto;bottom:100px;left:0;width:100%;z-index:1000}.slideCaptchaContainer .panel .puzzleImg{display:block;position:absolute;left:0;top:0;width:auto;height:100%}.slideCaptchaContainer .panel .bgImg{display:block;width:100%;border-radius:2px}.slideCaptchaContainer .control{position:relative;height:40px;border-radius:2px;border:1px solid #e4e7eb;background-color:#f7f9fa}.slideCaptchaContainer .control .slided{display:block;box-sizing:border-box;position:absolute;top:0;left:0;width:0;height:40px;border-radius:2px}.slideCaptchaContainer .control .slider{touch-action:none;position:absolute;top:0;left:0;width:40px;height:100%;border-radius:2px;background-color:#fff;box-shadow:0 0 3px rgba(0,0,0,.3);text-align:center;line-height:40px;color:#777;background-size:20px;font-size:25px;cursor:pointer}.slideCaptchaContainer .control .slider img{width:100%;height:100%}.slideCaptchaContainer .control .tips{text-align:center;line-height:40px;color:#45494c;font-size:20%}.slideCaptchaContainer .slider-moving .slided{border:1px solid #1e90ff;border-right:0;background-color:#add8e6;transition:background .2s linear}.slideCaptchaContainer .slider-end .slider,.slideCaptchaContainer .slider-moving .slider{color:#777;border:none;border-radius:0}.slideCaptchaContainer .slider-end .slided{border:1px solid transparent;border-right:0;background-color:transparent}.slideCaptchaContainer .slider-error .slided{border:1px solid #f57a7a;border-right:0;background-color:#fce1e1}.slideCaptchaContainer .slider-success .slided{border:1px solid #006400;border-right:0;background-color:#8fbc8f}.slideCaptchaContainer .reset{height:60px;width:100%;display:flex;align-items:center}.slideCaptchaContainer .reset .reset-btn{width:60px;height:40px}